#contact-form{margin-top:1em;display:grid;grid-gap:.5em;height:25em;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(3, 1fr) 2fr 1fr}#contact-form button{width:auto;grid-column:1 / 2;grid-row:5 / 6}#contact-form button svg{margin-left:.5em}#contact-form #input-name{grid-column:1 / 2;grid-row:1 / 2}#contact-form #input-subject{grid-column:2 / 3;grid-row:1 / 2}#contact-form #input-email{grid-column:1 / 3;grid-row:2 / 3}#contact-form #textarea-message{grid-column:1 / 3;grid-row:3 / 5}@media only screen and (min-device-width: 320px) and (max-device-width: 556px), screen and (min-device-height: 320px) and (max-device-height: 556px){#header-contact{padding:1em 1em 0 1em}#contact-form{padding:1em}#contact-form #input-name{grid-column:1 / 3;grid-row:1 / 2}#contact-form #input-subject{grid-column:1 / 3;grid-row:2 / 3}#contact-form #input-email{grid-column:1 / 3;grid-row:3 / 4}#contact-form #textarea-message{grid-column:1 / 3;grid-row:4 / 5}#contact-form button{grid-column:1 / 3;grid-row:5 / 6}}
