*{margin:0;padding:0;font-family:"Raleway"}h1{font-size:2em}h1 p{color:#303841}h2{color:#707070;font-size:1.6em}body{min-width:100%;min-height:100vh;background:linear-gradient(#13547A, #80D0C7)}input,textarea{background:#F4F4F4;color:#777777;font-size:1.3em;padding:.5em;border:none}textarea{resize:none}svg{width:2.5em;height:2.5em;fill:#303841}a{text-decoration:none}a:hover{opacity:.8}#app{margin:auto;padding:4em 0;width:80%;min-height:calc( 100vh - $space-1 - $space-1)}.flex-column{flex-direction:column}button{border:none;display:flex;justify-content:center;align-items:center;color:#fff;background-color:#13547A;padding:.6em  0;font-weight:800;font-size:1.3em;width:15em;cursor:pointer}button svg{fill:#ffffff;width:1.3em;height:1.3em}button:hover{opacity:0.8}.transition-page-appear #nav .link svg{opacity:0}.transition-page-appear #nav .link .triangulo.active{border-bottom:0em solid white}.transition-page-appear #content{padding:0 3em;max-height:0}.transition-page-appear-active #nav .link svg{opacity:1;transition:opacity 300ms linear}.transition-page-appear-active #nav .link .triangulo.active{border-bottom:1em solid white;transition:border-bottom 300ms linear}.transition-page-appear-active #content{padding:3em;max-height:100vh;transition:padding 600ms linear, max-height 600ms linear}.transition-page-appear-done{max-height:none}.transition-page-exit #nav{height:4.25em}.transition-page-exit #nav .link .triangulo.active{border-bottom:1em solid white}.transition-page-exit #nav #content{padding:3em;max-height:100vh}.transition-page-exit-active #nav{height:0em;transition:height 1ms linear 300ms}.transition-page-exit-active #nav .link .triangulo.active{border-bottom:0em solid white;transition:border-bottom 300ms linear}.transition-page-exit-active #content{padding:0 3em;max-height:0;transition:padding 300ms linear, max-height 300ms linear}.transition-page-enter #nav{height:0em}.transition-page-enter #nav .link svg{opacity:0}.transition-page-enter #nav .link .triangulo.active{border-bottom:0em solid white}.transition-page-enter #content{padding:0 3em;max-height:0}.transition-page-enter-active #nav{height:4.25em;transition:height 1ms linear 325ms}.transition-page-enter-active #nav .link svg{opacity:1;transition:opacity 300ms linear 350ms}.transition-page-enter-active #nav .link .triangulo.active{border-bottom:1em solid white;transition:border-bottom 300ms linear 350ms}.transition-page-enter-active #content{padding:3em;max-height:100vh;transition:padding 600ms linear 350ms, max-height 600ms linear 350ms}.transition-page-enter-done{max-height:none}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:#eeeeee}::-webkit-scrollbar-thumb{background:#888}::-webkit-scrollbar-thumb:hover{background:black}@media only screen and (min-device-width: 320px) and (max-device-width: 556px), screen and (min-device-height: 320px) and (max-device-height: 556px){#app{width:100%;padding:0;padding-top:1em}.transition-page-appear #content{padding:0}.transition-page-appear-active #content{padding:0}.transition-page-enter #content{padding:0}.transition-page-enter-active #content{padding:0}.transition-page-exit #content{padding:0}.transition-page-exit-active #content{padding:0}}
