.cover{position:absolute;right:0;top:0;height:100%;width:20em}.cover .arrow{display:flex;justify-content:center;align-items:center;right:0;top:calc( 50% - 1.75em);position:absolute;background:#ffffff;height:2.5em;width:2.5em;transition:width 300ms linear}.cover .arrow svg{cursor:pointer;width:1.3em;height:2.5em}.cover .arrow:hover{width:3em}.cover img{width:100%;height:100%}.social-icon{padding:.5em}#home{overflow:hidden}#home h1{margin-top:1.25em}#home h2{color:#707070;padding-left:1em;margin-bottom:1.25em}#home button{margin-left:calc( (100% - 20em - 7.5em - 3em ) * .5)}@media screen and (max-width: 1280px){.cover{width:15em}#home h1{font-size:1.6em}#home h2{font-size:1.3em}#home button{font-size:1em}}@media only screen and (max-device-width: 556px) and (min-device-width: 320px), screen and (max-device-height: 556px) and (min-device-height: 320px){.cover{position:inherit;width:100vw;height:100vw}.social-icon svg{margin:1.25em}#home h1{margin-left:1.25em}#home h2{margin-left:1.25em}#home button{margin:0 auto 3em auto}}
